import { GlobalSubscriptionService } from './../../app/globalSubcription.service';
import { Subscription } from 'rxjs/Subscription';
import { LocalStorageService } from './../../core/service/local-storage.service';
import { HttpUserAuthUtilsService } from './../../core/http_wrapper/http-userauth-utils.service';
import { HttpUserUtilsService } from './../../core/http_wrapper/http-user-utils.service';
import { ResizeOptions, ImageResult } from 'ng2-imageupload';
import { MediaResponseDtoList } from './../../core/models/mediaResponseList';
import { UpcomingGymRelatedClasses, upcomingGymBranchClasses } from './../../core/models/upcomingGymBranchClasses';
import { TrainerGymDetail } from './../../core/models/gymtrainerDetailResponse';
import { GymViewData } from './../../core/models/gymDetailResponse';
import { HttpSuccesFailureResponse } from './../../core/http_wrapper/http_wrapper_response';
import { Component, OnInit } from '@angular/core';


import { Modal, BSModalContext } from 'angular2-modal/plugins/bootstrap';
import { Constants, InfiniteScroll } from "core/constants";
import { ClassDetail } from "./../../core/models/classListResponse";
import { Router } from '@angular/router';
import { Overlay, overlayConfigFactory } from 'angular2-modal';
import { ConfirmpopupComponent } from "classes/confirmationPopup/confirmationPopup.component";
import { ViewAllSlotsComponent } from 'viewAllSlotsPopup/viewallslots.component';
import { UserSession } from '../../core/sessions';
import { PlaceDetail } from 'core/models/allPlacesApiResponse';
import { gymBranchResponseList } from 'core/models/searchResultGymResponse';
import { TrainerDetail } from 'core/models/trainerDetailResponse';
import { HttpCommonUtilsService } from 'core/http_wrapper/http-common-utils.service';
import { forEach } from '@angular/router/src/utils/collection';
import { BaseComponent } from 'app/base/base.component';




@Component({
  selector: 'app-gymUpcomingclass',
  templateUrl: './gymUpcomingClasses.component.html',
  styleUrls: ['./gymUpcomingClasses.component.css']
})
export class GymUpcommingClass extends BaseComponent implements OnInit, HttpSuccesFailureResponse {
  coverMainFile: File;
  coverFileList: File[] = [];
  profileMainFile: File;
  profileFileList: File[] = [];
  gymDetailresponse: GymViewData;
  trainerDetailResponse: TrainerGymDetail[];
  coverfilelength: boolean = false;
  src: string = "";
  profsrc: string = "";
  profilefilelength: boolean = false;
  upcommingbranchClasses: UpcomingGymRelatedClasses[] = []
  MediaResponseDtoList: MediaResponseDtoList[] = [];
  gallerycount: number = 0;
  subscription: Subscription;
  mediauploadsuccess: boolean = false;
  classnumber: number = 0;
  resizeOptions: ResizeOptions = {
    resizeMaxHeight: 253,
    resizeMaxWidth: 1200
  };
  listIsLaoding: boolean = false;
  currentPage: number = 0;
  isLoadMoreData: boolean = true;
  deleteClassById: ClassDetail;
  isLoaded: boolean = false;
  filterData: Array<any> = [];
  branchList:any;
  trainerList: any;
  trainerSelected :any ;
  public throttle = InfiniteScroll.throttle;
  public scrollDistance = InfiniteScroll.scrollDistance;
  public scrollUpDistance = InfiniteScroll.scrollUpDistance;
  showClassNumberFlag = false;
  showNoClassesAfterFilterStub = false;

  trainerFilterValue:any ;
  branchFilterValue:any ;
  classTypeFilterValue:any ;
  classNameFilterValue:any ='';
  isMasterGym;
  gymList=[];
  gymId;


  
  constructor(private router: Router, private globalSubscriptionService: GlobalSubscriptionService, private http_common: HttpCommonUtilsService, private http_userauth: HttpUserAuthUtilsService, private modal: Modal) {
    super(http_userauth)
   }


  ngOnInit() {
    this.loadAdminDataIfLoggenIn();
    this.isMasterGym = LocalStorageService.isMasterGym();
    this.gymId = LocalStorageService.getUserId();
    if(this.isMasterGym){
      this.http_userauth.getGymList(this,true);
    }
    this.listIsLaoding = true;
    this.findGymClasses();
    this.http_userauth.getAllPlaceslistByGymId(this.gymId,this, false);  //API for all branches of a gym
    this.http_common.getTrainerList('&gymId=' + this.gymId,this,true); //trainer list API

  }
  navigate() {
    this.router.navigate(['class'])
  }
  deleteclass(value: ClassDetail) {
    let message = Constants.deleteClassMessage;
    let title = Constants.deleteClassTitle;
    this.deleteClassById = value;
    let parent = this;
    return this.modal.open(ConfirmpopupComponent, overlayConfigFactory({ message, title, parent }, BSModalContext));
  }
  coverImageSelected(imageResult: ImageResult) {
    if (this.coverMainFile) {
      let index = this.coverFileList.indexOf(this.coverMainFile);
      this.coverFileList.splice(index, 1);
    }

    this.coverMainFile = imageResult.file;
    this.coverFileList.push(this.coverMainFile);
    this.coverfilelength = false;
    this.uploadPic(LocalStorageService.getUserId(), 'cover')
  }
  profileImageSelected(imageResult: ImageResult) {
    if (this.profileMainFile) {
      let index = this.profileFileList.indexOf(this.profileMainFile);
      this.profileFileList.splice(index, 1);
    }
    this.profsrc = imageResult.resized
      && imageResult.resized.dataURL
      || imageResult.dataURL;
    this.profileMainFile = imageResult.file;
    this.profileFileList.push(this.profileMainFile)
    this.profilefilelength = false;
    this.uploadPic(LocalStorageService.getUserId(), 'profile')
  }
  uploadPic(gymid: any, type: string) {
    const formData = new FormData();

    if (type == 'cover') {
      if (!this.coverFileList.length) return;
      Array.from(Array(this.coverFileList.length).keys()).map(x => {
        formData.append("media", this.coverFileList[x]);
        formData.append("mediaFor", "gymBackground");
        formData.append("mediaType", "Image");
        formData.append("objectDataId", gymid);
        formData.append("objectType", "gym");
      });
    }
    if (type == 'profile') {
      if (!this.profileFileList.length) return;
      Array.from(Array(this.profileFileList.length).keys()).map(x => {
        formData.append("media", this.profileFileList[x]);
        formData.append("mediaFor", "GymProfile");
        formData.append("mediaType", "Image");
        formData.append("objectDataId", gymid);
        formData.append("objectType", "gym");
      });

    }

    this.http_userauth.uploadMedia(formData, this, true);
  }
  findGymClasses() {
    let term = this.classNameFilterValue;
    let classType = "all";
    this.http_userauth.getTrainerMyClassesDetail(term, this.gymId, this.currentPage, classType, this, true)
}
  onScrollUp(event) {
    //     if(this.isLoadMoreData && this.lastScrollDistance<event.currentScrollPosition && !this.listIsLaoding){
    //       this.listIsLaoding=true;
    //       this.lastScrollDistance=event.currentScrollPosition;
    //       this.currentPage=this.currentPage+1;
    // this.http_userauth.getTrainerClassesDetail(LocalStorageService.getUserId(),this.currentPage,this,false);
    //     }  
  }
  editclasspopup(value: ClassDetail) {
    this.router.navigate(['class', value.classId]);
  }
  confirmAction() {
    this.http_userauth.classDeleteByClassId(this.deleteClassById.classId, this, true)

  }
  cancelAction() {

  }

  pushClasses(element:UpcomingGymRelatedClasses) { 
    if((this.trainerFilterValue==-1 || this.trainerFilterValue==undefined) && 
      (this.branchFilterValue==-1 || this.branchFilterValue==undefined) &&
      (this.classTypeFilterValue==-1 || this.classTypeFilterValue==undefined) &&
      (this.classNameFilterValue=='' || this.classNameFilterValue==undefined))
      { this.showClassNumberFlag = false;   //shows total number of classes only when no filter is selected
        this.showNoClassesAfterFilterStub = false;

       this.upcommingbranchClasses.push(element);
      }
      else
      {
        this.showNoClassesAfterFilterStub = true;
        let pushToList=false; this.showClassNumberFlag = pushToList;
        if(this.trainerFilterValue >-1){ this.showClassNumberFlag = true;
          if(element.trainerDetail&&this.trainerFilterValue == element.trainerDetail.id) {
            pushToList = true; 
            }else{
              pushToList = false; 
              return
              
            }
        } 
        
        if(this.branchFilterValue >-1){ this.showClassNumberFlag = true;
          if(element.gymBranchDetail&&this.branchFilterValue==element.gymBranchDetail.branchId) {
            pushToList = true; 
            }
            else{
              pushToList = false; 
              return
            }
        } 
        
        if(this.classTypeFilterValue >-1){ this.showClassNumberFlag = true;
          if(this.classTypeFilterValue==element.verifyStatus) {
            pushToList = true; 
          }
          else{
            pushToList = false; 
            return

          }
          
        }  
        if(this.classNameFilterValue && this.classNameFilterValue.length>0){ this.showClassNumberFlag = true;
          if(element.title.toLowerCase().indexOf(this.classNameFilterValue.toLowerCase())>-1) {
            pushToList = true; 
            }
            else{
              pushToList = false; 
              return
            } 
        } 
       if(pushToList){ 
        this.pushIfnotExist(this.upcommingbranchClasses,element)
        //this.upcommingbranchClasses.push(element);
       }
       

      

      } 

  }

  pushIfnotExist(upcommingbranchClasses,element){
    if(upcommingbranchClasses&&upcommingbranchClasses.length>0){
      let exist =false;
      upcommingbranchClasses.forEach(ele => {
         if(ele.classId==element.classId){
           exist =true;
         }
        
      });
      if(!exist){
        this.upcommingbranchClasses.push(element);

      }

    }else{
      this.upcommingbranchClasses.push(element);

    }

  }

  

  
  onFilterApply(event,type)
   {
    
    this.currentPage=0;
    this.upcommingbranchClasses=[];

    if(type==='TRAINER')
    { 
      this.trainerFilterValue = event.target.value;
    }
    else if(type==='BRANCH')
    {
      this.branchFilterValue = event.target.value;
    }
    else if(type==='CLASSTYPE')
    { 
      this.classTypeFilterValue = event.target.value;
    }
    else if(type==='CLASSNAME')
    { 
      this.classNameFilterValue = event.target.value; 
    }
    this.findGymClasses();

  } //onFilterApply function closing

  search(e){
    e.preventDefault(); 
e.stopPropagation();
  }
  selectGym(event){
     this.gymId = event.target.value;
    if(this.gymId==-1){
      this.gymId = LocalStorageService.getUserId();
    }
   
      this.trainerFilterValue = -1;
   
      this.branchFilterValue = -1;
  
      this.classTypeFilterValue = -1;
    
      this.classNameFilterValue = ""; 
    
    this.http_userauth.getAllPlaceslistByGymId(this.gymId,this, false);  //API for all branches of a gym
    this.http_common.getTrainerList('&gymId=' + this.gymId,this,true); //trainer list API
    this.currentPage=0;
    this.upcommingbranchClasses=[];
    this.findGymClasses();


  }
  
  onSuccess(type: any, responsedata: any) {
    
    if (type == Constants.FindClassesGym) {
      this.listIsLaoding = false;
      this.upcommingbranchClasses = responsedata;
      this.classnumber = this.upcommingbranchClasses.length;
    }
    if(type == Constants.gymListApi){
      this.gymList = responsedata.data;
      this.gymList = this.gymList.filter((element) => {
        return element.id != LocalStorageService.getMasterGymId();
     });
    }
    if (type == Constants.allPlacesApi) {
      this.branchList = responsedata.data;
    }

    if (type == Constants.trainerFilterListApi) {
      
      this.trainerList = responsedata.data;
    }
    


    if (type == Constants.mediaUpdate) {
      this.mediauploadsuccess = true;
    }
    if (type == Constants.ClassDelete) {
      
      let classTobeDeleted:number = -1;
      this.upcommingbranchClasses.forEach(element => {
        if(element.classId === this.deleteClassById.classId){
          
          classTobeDeleted = this.upcommingbranchClasses.indexOf(element);
        }
      });

      if(classTobeDeleted > -1){
        
        this.upcommingbranchClasses.splice(classTobeDeleted,1);
      }
    }
    
    if (type == Constants.TrainerClassesDetailApi) {

     if(this.currentPage==0){
       this.upcommingbranchClasses = [];
      }
      this.listIsLaoding = false;
      var temp_list: any[] = responsedata.data;
       temp_list.forEach(element => {
         if (UserSession.checkisPastDate(element.maxToDate)) {
           element.verifyStatus = 4
         }
         this.pushClasses(element);
     //    this.upcommingbranchClasses.push(element);
        });
       if (temp_list.length === 0) {
        this.isLoadMoreData = false;
      } else {
        this.isLoadMoreData = true;
      }
       if(this.isLoadMoreData && this.upcommingbranchClasses.length<10){
        this.onScrollDown();
      }
      if(this.currentPage==0){
        this.classnumber = temp_list[0].count;
      }  
    }
    

  }
  onFailure(type: any, response: string) {
    this.listIsLaoding = false;
  }
  routetoEditClass() {

  }
  openslots(value) { 
    this.router.navigate(['class', value.classId,3]);
    // let classId = value.classId;
    // return this.modal.open(ViewAllSlotsComponent, overlayConfigFactory({ classId }, BSModalContext));

  }
  onScrollDown(){
    if(this.isLoadMoreData &&  !this.listIsLaoding){
          this.listIsLaoding=true;
          this.currentPage=this.currentPage+1;
          let term = this.classNameFilterValue;
         let classType = "all";
           this.http_userauth.getTrainerMyClassesDetail(term, this.gymId, this.currentPage, classType, this, false);
        } 
  }
  onUp(){

  }


}


